import { Component, OnInit } from '@angular/core';
import { Mission } from './shared/mission.model';
import { DataService } from './services/data.service';
import { AfterViewInit, ViewChild } from '@angular/core';

import { FiltersComponent } from './filters/filters.component';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{

  missions: Mission[]

  mainFilter: any

  currentSorting: string

  @ViewChild('filtersComponent')
  filtersComponent: FiltersComponent;


  launchYearFilters: any[] = [
    {'name': 2004, value:2004},
    {'name': 2005, value:2005},
    {'name': 2006, value:2006},
    {'name': 2007, value:2007},
    {'name': 2008, value:2008},
    {'name': 2009, value:2009},
    {'name': 2010, value:2010},
    {'name': 2011, value:2011},
    {'name': 2012, value:2012},
    {'name': 2013, value:2013},
    {'name': 2014, value:2014},
    {'name': 2015, value:2015},
    {'name': 2016, value:2016},
    {'name': 2017, value:2017},
    {'name': 2018, value:2018},
    {'name': 2019, value:2019},
    {'name': 2020, value:2020}
  ]

  successfulLaunchFilters: any[] = [
    { name:'True', value:'true' },
    { name:'False', value:'false' }
  ]

  successfulLandingFilters: any[] = [
    { name:'True', value:'true' },
    { name:'False', value:'false' }
  ]

  originalData: any = []

  constructor(private dataService: DataService){  }

  ngOnInit(){


    this.dataService.getData(null).subscribe(data => {
      this.originalData = data
      this.mainFilter = {
        search: '',
        launchYearFilters: this.launchYearFilters.slice(0),
        successfulLaunchFilters: this.successfulLaunchFilters.slice(0),
        successfulLandingFilters: this.successfulLandingFilters.slice(0)
      }
      this.missions = this.originalData.slice(0)
    })
  }

  onFilterChange(data:any){
    this.dataService.getData(data.filterCriteria).subscribe(data => {
      this.originalData = data
      this.mainFilter = {
        search: '',
        launchYearFilters: this.launchYearFilters.slice(0),
        successfulLaunchFilters: this.successfulLaunchFilters.slice(0),
        successfulLandingFilters: this.successfulLandingFilters.slice(0)
      }
      this.missions = this.originalData.slice(0)
    })
  }
}